<template>
  <div class="main" id="flowbox-outer">
    <!-- <h1>This is an about page</h1> -->
    <!-- <div> -->
    <!-- <div :style="{ marginBottom: '16px' }">
        <a-button @click="add">ADD</a-button>
    </div>-->
    <div class="headerbox">
      <!-- <a-button type="primary" class="workflow-add-btn" @click="showModal">添加工作流 </a-button> -->
      <span style="font-weight: 700;font-style: normal;font-size: 16px;">工作流列表</span>
      <!-- 添加工作流弹出框 -->
      <a-modal
        class="addworkflow-modal motify-modal"
        title="新增工作流"
        v-model="visible"
        @ok="handleOk"
      >
        <a-form :form="formadd" @submit="handleSubmit">
          <a-form-item label="工作流名称：" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
            <a-input
              class="motify-modal-input"
              placeholder="请输入工作流名称"
              @blur.prevent="addchangeInput()"
              v-decorator="[
                  'workflowName',
                  {rules: [{ required: true, message: '新工作流名称为空' }]}
                ]"
            />
          </a-form-item>
          <a-form-item label="备注：" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
            <a-textarea
              @blur.prevent="addchangeTextarea()"
              placeholder="请输入备注内容"
              v-decorator="[
                  'workflowRemark',
                  {rules: [{ required: true, message: '备注为空' }]}
                ]"
            />
          </a-form-item>
          <a-form-item label="是否允许中断:" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
            <a-switch
              :checked="Boolean(this.workFlowAdd.interrupt) "
              v-decorator="[
                  'checkedswitch',
                ]"
              @change="onChangecheckedswitch"
            >
              <a-icon type="check" slot="checkedChildren" />
              <a-icon type="close" slot="unCheckedChildren" />
            </a-switch>
          </a-form-item>
        </a-form>
      </a-modal>

      <!-- 角色管理的弹出框 -->
      <a-modal
        class="Connection-modal motify-modal"
        title="关联菜单"
        v-model="visibleRole"
        :footer="null"
      >
        <div>
          <div style="overflow:hidden">
            <a-button type="primary" class="role-modal-add-btn" @click="OpenNewmeun">添加参数</a-button>
          </div>
          <a-table
            rowKey="roleId"
            :bordered="showBorder"
            :dataSource="ConnectionModaldata"
            :columns="columnsConnectionModal"
            :pagination="showPagination"
            :scroll="{ y: 200}"
          >
            <!-- <template v-for="col in ['roleMark', 'roleName']" :slot="col" slot-scope="text, record, index">
                    <div :key="col">
                      <a-input
                        v-if="record.editable"
                        style="margin: -5px 0"
                        :value="text"
                        @change="e => handleChange(e.target.value, record.roleId, col)"
                      />
                      <template v-else>{{text}}</template>
                    </div>
            </template>-->

            <template slot="roleName" slot-scope="text, record">
              <a
                href="javascript:;"
                style="text-decoration: none;color:#00aaee"
                @click="ReviseRole(record)"
              >{{text}}</a>
            </template>

            <template slot="operation" slot-scope="text, record">
              <!-- <span class='editable-row-operations'>
                      <span v-if="record.editable">
                        <a @click="() => save(record.roleId)" style="margin-right:2px;">保存</a>
                        <a-popconfirm title='确定要取消吗?' @confirm="() => cancel(record.roleId)">
                          <a>取消</a>
                        </a-popconfirm>
                      </span>
                      <span v-else>
                        <a @click="() => edit(record)">修改</a>
                      </span>
                    </span>
              <b style="font-weight: 400;color: #ccc;margin:0 2px 0  4px">|</b>-->
              <a-popconfirm
                class="operation-choose"
                v-if="ConnectionModaldata.length"
                title="确定要删除吗?"
                @confirm="() => onDeleteConnectionmenu(record)"
              >
                <a href="javascript:;" style="text-decoration: none;color:#00aaee">删除</a>
              </a-popconfirm>
            </template>
          </a-table>
        </div>
      </a-modal>

      <!-- 角色管理的详情 -->
      <a-modal
        class="addworkflow-modal motify-modal"
        title="修改角色"
        v-model="visibleRolename"
        @ok="handleOkRole"
      >
        <a-form :form="formRole" @submit="handleSubmitRole">
          <a-form-item label="角色名称：" :label-col="{ span: 6 }" :wrapper-col="{ span: 12 }">
            <a-input
              class="motify-modal-input"
              placeholder="请输入角色名称"
              @blur.prevent="addchangeInput()"
              v-decorator="[
                  'roleName',
                  {rules: [{ required: true, message: '新角色名称为空' }]}
                ]"
            />
          </a-form-item>
          <a-form-item label="角色标识：" :label-col="{ span: 6 }" :wrapper-col="{ span: 12 }">
            <a-textarea
              @blur.prevent="addchangeTextarea()"
              placeholder="请输入角色标识"
              v-decorator="[
                  'roleMark',
                  {rules: [{ required: true, message: '角色标识为空' }]}
                ]"
            />
          </a-form-item>
        </a-form>
      </a-modal>

      <!--角色管理的弹出框里面的新增菜单 -->
      <a-modal
        class="Connection-modal-NewMeun motify-modal"
        title="添加菜单"
        v-model="visibleAddmeun"
        @ok="handleOkaddmeun"
      >
        <a-form :form="formaddmenu" @submit="handleSubmitaddmenu">
          <a-form-item label="角色名称：" :label-col="{ span: 4 }" :wrapper-col="{ span: 16 }">
            <a-input
              class="motify-modal-input"
              placeholder="请输入角色名称"
              @blur.prevent="addNewMeunlogochangeInput()"
              v-decorator="[
                  'workflowName',
                  {rules: [{ required: true, message: '角色名称为空' }]}
                ]"
            />
          </a-form-item>

          <a-form-item label="角色标识：" :label-col="{ span: 4 }" :wrapper-col="{ span: 16 }">
            <a-textarea
              @blur.prevent="addNewMeunNamechange()"
              placeholder="请输入角色标识"
              v-decorator="[
                  'workflowRemark',
                  {rules: [{ required: true, message: '角色标识为空' }]}
                ]"
            />
          </a-form-item>
        </a-form>
      </a-modal>

      <div class="right">
        <!-- <a-button type="primary" class="workflow-role-btn" @click="showRoleModal">角色管理</a-button> -->
        <a-button type="primary" class="workflow-add-btn" @click="showModal">添加工作流</a-button>
        <a-input-search placeholder="搜索模块描述" style="width: 200px" @search="onSearch" />
      </div>
    </div>

    <!-- 点击flowname 的弹框 -->
    <a-modal
      class="revise-workflow-modal motify-modal"
      title="修改工作流"
      v-model="visibleReviseworkflow"
      @ok="handleOkReviseworkflow"
    >
      <!-- <div>
            {{this.flownameclickRecode}}
      </div>-->

      <a-form :form="form" @submit="handleSubmitReviseworkflow">
        <a-form-item label="工作流名称：" :label-col="{ span: 4.5 }" :wrapper-col="{ span: 12 }">
          <a-input
            class="motify-modal-input"
            placeholder="请输入工作流名称"
            ref="workflowName"
            v-decorator="[
                  'workflowName',
                   {
                    rules: [{ required: true, message: '新工作流名称为空' }]
                   }
                ]"
          />
        </a-form-item>
        <a-form-item label="备注：" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" >
          <a-textarea
            placeholder="请输入备注内容"
            v-decorator="[
                  'workflowRemark',
                  {rules: [{ required: true, message: '备注为空' }]}
                ]"
          />
        </a-form-item>
        <a-form-item label="是否允许中断:" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" >
          <!--  :defaultChecked="Boolean(this.fields.workSwitch.value) "   :defaultChecked="this.fields.workSwitch.value==1?true:false "-->
          <a-switch
            :checked="Boolean(this.fields.workSwitch.value) "
            v-decorator="[
                  'workSwitch',
                ]"
            @change="onChangeAddcheckedswitch"
          >
            <a-icon type="check" slot="checkedChildren" />
            <a-icon type="close" slot="unCheckedChildren" />
          </a-switch>
        </a-form-item>
      </a-form>
    </a-modal>

    <!-- 页面主表 -->
    <a-table
      rowKey="wftId"
      :bordered="showBorder"
      :dataSource="dataSource"
      :columns="columns"
      :scroll="{ y: 330}"
      :pagination="showPagination"
      style="height:calc(100vh - 220px);"
    >
      <!-- <template slot="name" slot-scope="text, record">
        <editable-cell :text="text" @change="onCellChange(record.key, 'name', $event)"/>
      </template>-->

      <!-- 是否启用注释了，勿删 -->
      <template slot="interrupt" slot-scope="text">
            <a href="javascript:;" style="text-decoration: none;color:#333">
              <a-icon type="check" style="color:green" v-if="text" />
              <a-icon type="close" v-else />
            </a>
      </template>

      <template slot="order" slot-scope="text">
        <a href="javascript:;" style="text-decoration: none;color:#333">{{text}}</a>
      </template>

      <template slot="wftName" slot-scope="text, record">
        <a
          href="javascript:;"
          @click="flownameclick(record)"
          style="text-decoration: none;cursor:pointer;color:#00aaee"
        >{{text}}</a>
      </template>

      <template slot="operation" slot-scope="text, record">
        <a
          href="javascript:;"
          @click="editTemp(record.wftId)"
          style="text-decoration: none;color:#00aaee;padding-left:5px;"
        >设计</a>
        <!-- <b style="font-weight: 400;color: #ccc;">|</b> -->
        <a-popconfirm
          class="operation-choose"
          v-if="dataSource.length"
          title="确定要删除吗?"
          @confirm="() => onDelete(record)"
        >
          <a href="javascript:;" style="text-decoration: none;color:#00aaee">删除</a>
        </a-popconfirm>

        <!-- <a
          href="javascript:;"
          style="text-decoration: none;color:#00aaee"
          @click="dynamicAdd(record.wftId)"
          ref="dynamicAddbtn"
        >审批动态</a> -->
      </template>
    </a-table>

    <div class="pagination-box">
      <a-pagination
        @change="onChange"
        :pageSizeOptions="pageSizeOptions"
        :total="total"
        :showTotal="total => `共 ${total} 条`"
        showSizeChanger
        showQuickJumper
        :pageSize="pageSize"
        v-model="current"
        @showSizeChange="onShowSizeChange"
      >
        <template slot="buildOptionText" slot-scope="props">
          <span v-if="props.value!=='50'">{{props.value}}条/页</span>
          <span v-if="props.value==='50'">全部</span>
        </template>
      </a-pagination>
    </div>
  </div>
</template>

<script>
import {
  getWorkFlowList,
  WorkFlowListadd,
  WorkFlowListAddOrRevise,
  getWorkFlowListDetail,
  WorkFlowListupdate,
  WorkFlowListDelete,
  getRoleList,
  RoleaAdd,
  RoleListupdate,
  RoleDelete
} from "../../api/UserApi";

import moment from "moment";
import Dynamicspage from "../showflow/Dynamics";
export default {
  components: {
    Dynamicspage
  },
  data() {
    return {
      Rolerevise: {
        roleName: "",
        roleMark: "",
        roleId: 0
      },
      fieldsRole: {
        roleName: {
          value: ""
        },
        roleMark: {
          value: ""
        }
      },
      fields: {
        workflowName: {
          value: ""
        },
        workflowRemark: {
          value: ""
        },
        workSwitch: {
          value: 0
        }
      },
      visibleRolename: false, //角色管理名称点击的详情
      visibleRole: false, //角色管理的弹出框
      showBorder: false,
      showPagination: false,
      visibleReviseworkflow: false,

      ConnectionNewMeundatacolumns: [
        {
          title: "菜单",
          dataIndex: "menu",
          width: "200px",
          align: "left"
        }
      ],
      count: 2,
      dataSource: [
        // {
        //   key: "1",
        //   order: "1",
        //   wftId: "1",
        //   wftName: "John Brown",
        //   createTime: "2019-07-08",
        //   updateTime: "2019-07-08",
        //   interrupt: false,
        //   remark: "bababbaabb"
        // },
        // {
        //   key: "2",
        //   order: "2",
        //   wftId: "2",
        //   wftName: "John Brown",
        //   createTime: "2019-07-08",
        //   updateTime: "2019-07-08",
        //   interrupt: false,
        //   remark: "bababbaabb"
        // }
      ],
      columns: [
        {
          //表格头部样式的设置
          title: "#",
          dataIndex: "order",
          width: "50px",
          align: "center",
          scopedSlots: { customRender: "order" }
        },
        {
          title: "工作流名称",
          dataIndex: "wftName",
          scopedSlots: { customRender: "wftName" },
          width: "200px",
          align: "center"
        },
        // {
        //   title: '创建人',
        //   dataIndex: 'createperson',
        //   width: '150px',
        //   align:'center'
        // },
        // {
        //   title: '更新人',
        //   dataIndex: 'updateperson',
        //   width: '150px',
        //   align:'center'
        // },
        {
          title: "创建时间",
          dataIndex: "createTime",
          width: "200px",
          align: "center"
        },
        {
          title: "更新时间",
          dataIndex: "updateTime",
          width: "200px",
          align: "center"
        },
        {
          title: "是否启用",
          dataIndex: "interrupt",
          scopedSlots: { customRender: "interrupt" },
          width: "100px",
          align: "center"
        },
        {
          title: "备注",
          dataIndex: "remark",
          width: "200px",
          align: "center"
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" },
          align: "center",
          width: "100px"
        }
      ],
      columnsConnectionModal: [
        //角色管理的弹出框
        {
          title: "角色名称",
          dataIndex: "roleName",
          scopedSlots: { customRender: "roleName" },
          // width: '100px',
          width: "30%",
          align: "center"
        },
        {
          title: "角色标识",
          dataIndex: "roleMark",
          // scopedSlots: { customRender: 'roleMark' },
          // width: '100px',
          //  width: '200px',
          align: "center"
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: { customRender: "operation" },
          // width: '120px',
          width: "20%",
          align: "center"
        }
      ],
      ConnectionModaldata: [],
      //  ConnectionModaldata:[{  //角色管理的弹出框的数据
      //   key: '0',
      //   menu:"CRM",
      //   value:'obj'
      // },
      // {
      //    key: '1',
      //   menu:"市场营销",
      //     value:'obj'
      // }
      // ],
      pageSizeOptions: ["10", "20", "30", "40", "50"],
      current: 1,
      pageSize: 10,
      pageNumber: 1,
      total: 0,
      keyword: "",
      visible: false,
      // visibleConnection:false,
      visibleAddmeun: false,
      visibleDesignConfirm: false,
      formLayout: "horizontal", //add弹出框的表单
      form: this.$form.createForm(this),
      formRole: this.$form.createForm(this), //角色名称点击的弹框
      formadd: this.$form.createForm(this),
      formaddmenu: this.$form.createForm(this), // 角色的第二个弹出框
      workFlowAdd: {
        remark: "",
        wftName: "",
        interrupt: 1
      },
      workFlowRevise: {
        wftId: 0,
        remark: "",
        wftName: "",
        interrupt: 1
      },
      // interrupt:true,
      workflowRectStart: {
        wftId: 0,
        viewNode: {
          rect: [
            {
              nodeId: 1,
              nodeName: "开始",
              nodeType: 0,
              x: 0,
              y: 0,
              width: 100,
              height: 100
            }
          ],
          line: [
            {
              lineId: 1,
              lineName: "线的名称",
              fromNodeId: 1,
              toNodeId: 9999
            }
          ]
        }
      },
      workSwitch: 0,
      cacheData: [],
      Deletedata: {
        //角色的删除
        roleId: 0,
        roleMark: ""
      }
    };
  },
  mounted() {
    console.log('list mounted')
    this.getListdata();
    // const panes = this.panes;
    // console.log(this.panes);
  },

  methods: {
    editTemp(wft_id) {
      console.log(wft_id);
      console.log(this.$route);
      if (this.$route.name == "tabs") return this.$store.dispatch("workflowListEditTemp", wft_id);
      let routeUrl = this.$router.resolve({
        name: "edit",
        query: {
          wft_id,
          access_token: this.$global.requestToken
        }
      });
      window.open(routeUrl.href, "_blank");
    },
    getListdata() {
      let page = this.pageNumber,
        size = this.pageSize,
        keyword = this.keyword,
        access_token = this.$global.requestToken;
      // console.log(this.$route.query.access_token);
      getWorkFlowList(page, size, keyword, access_token)
        .then(res => {
          // getWorkFlowList().then((res)=>{
          this.dataSource = res.data.records;
          let datalist = res.data.records;

          // let datalist = this.dataSource

          this.total = res.data.total;
          console.log(this.dataSource);
          datalist.forEach((item, i) => {
            this.dataSource[i].updateTime = moment(item.updateTime).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            this.dataSource[i].createTime = moment(item.createTime).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          });

          console.log(res.data);
          //  添加一个index
          this.dataSource.map((item, index) => {
            // console.log(item);
            item.order = index + 1;
          });
        })
        .catch(err => {
          throw err;
        });
    },
    // 删除按钮
    onDelete(record) {
      console.log(record);
      let key = record.wftId;
      const dataSource = [...this.dataSource];
      WorkFlowListDelete({ wftId: record.wftId }).then(res => {
        console.log(res);
        if (res.data.message == "success") {
          this.dataSource = dataSource.filter(item => item.wftId !== key);
          this.$message.success("删除工作流成功");
          this.getListdata();
        }
      });
    },
    // 审批动态
    dynamic(record) {
      console.log();
    },
    onSearch(value) {
      console.log(value);
      this.pageNumber = 1;
      this.keyword = value;
      this.getListdata();
    },
    onShowSizeChange(current, pageSize) {
      this.pageSize = pageSize;
      console.log(this.pageSize);
      console.log(current);
      this.getListdata();
    },
    // 右边的页码
    onChange(pageNumber) {
      this.pageNumber = pageNumber;
      console.log("Page: ", pageNumber);
      this.getListdata();
    },
    //添加工作流按钮
    showModal() {
      this.visible = true;
      this.formadd = this.$form.createForm(this);
      this.workFlowAdd.interrupt = 1;
      setTimeout(() => {
        //加了个定时器，避免了表单初始化的时候报错
        this.formadd.setFieldsValue({
          workflowName: "",
          workflowRemark: "",
          // checkedswitch:1
          checkedswitch: this.workFlowAdd.interrupt
        });
      }, 0);
    },
    // 工作流添加按钮----弹框---确定按钮
    handleOk(e) {
      console.log(e);

      console.log("工作流添加按钮----弹框---确定按钮", this.formadd);
      this.handleSubmit(e);
      // this.workflowRectStart.wftId =
      // WorkFlowListAddOrRevise().then((res)=>{
      //   console.log(res);
      // })
    },
    // 工作流添加按钮--弹框表单提交
    handleSubmit(e) {
      e.preventDefault();
      this.formadd.validateFields((err, values) => {
        if (!err) {
          this.visible = false;
          console.log("Received values of formadd: ", values);
          this.workFlowAdd.wftName = values.workflowName;
          this.workFlowAdd.remark = values.workflowRemark;
          this.workFlowAdd.interrupt = Number(values.checkedswitch);
          console.log("提交按钮", this.workFlowAdd);
          WorkFlowListadd(this.workFlowAdd).then(res => {
            this.$message.success("添加工作流成功");
            this.getListdata();
            //  }
          });
        }
      });

      //  console.log('工作流添加按钮');
    },
    // flowname里面的开关
    onChangeAddcheckedswitch(checked) {
      this.fields.workSwitch.value = Number(checked);
      console.log(this.fields.workSwitch.value);
    },
    //新增flow的开关
    onChangecheckedswitch(checked) {
      console.log(checked);
      this.workFlowAdd.interrupt = Number(checked);
    },

    handleSubmitReviseworkflow(e) {
      // e.preventDefault();
      // this.form.validateFields((err, values) => {
      //   if (!err) {
      //     console.log('Received values of form: ', values);
      //   }
      // });
    },

    handleSelectChange(value) {
      console.log(value);
      this.form.setFieldsValue({
        note: `Hi, ${value === "male" ? "man" : "lady"}!`
      });
    },
    // 角色列表获取
    getRoleListData() {
      getRoleList().then(res => {
        console.log(res);
        this.ConnectionModaldata = res.data;
      });
    },
    // 角色按钮
    showRoleModal() {
      this.visibleRole = true;
      this.getRoleListData();
    },
    // 角色的第一个弹框添加按钮
    OpenNewmeun() {
      this.visibleAddmeun = true;
      this.formaddmenu = this.$form.createForm(this); //初始化form表单
      // this.visibleConnection = false
    },
    //角色菜单的删除
    onDeleteConnectionmenu(record) {
      console.log(record);
      let key = record.roleId;
      const dataSource = [...this.ConnectionModaldata];
      this.ConnectionModaldata = dataSource.filter(item => item.roleId !== key);
      // let Deletedata = {}
      this.Deletedata.roleId = record.roleId;
      this.Deletedata.roleMark = record.roleMark;
      RoleDelete(this.Deletedata).then(res => {
        if (res.data.message == "success") {
          this.$message.success("删除角色成功");
          this.getRoleListData();
        }

        // console.log(res);
      });
    },
    handleOkaddmeun(e) {
      //角色的第二个弹框
      this.handleSubmitaddmenu(e);
      this.visibleAddmeun = false;
    },
    // 角色的第二个弹框--表单提交
    handleSubmitaddmenu(e) {
      e.preventDefault();
      this.formaddmenu.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          RoleaAdd({
            roleName: values.workflowName,
            roleMark: values.workflowRemark
          }).then(res => {
            if (res.data.message == "success") {
              this.$message.success("添加角色成功");
              this.getRoleListData();
            }
          });
        }
      });
    },
    confirm() {
      this.$confirm({
        title: "提示",
        content: "是否确定禁用该工作流",
        okText: "确认",
        cancelText: "取消"
      });
    },
    flownameclick(record) {
      console.log(record);
      if (record.wftId) {
        this.visibleReviseworkflow = true;
        //  this.flownameclickRecode = record
        getWorkFlowListDetail(record.wftId).then(res => {
          console.log(res);
          this.fields.workflowName.value = res.data.wftName;
          this.fields.workflowRemark.value = res.data.remark;
          this.fields.workSwitch.value = res.data.interrupt;
          this.switchtest = res.data.interrupt;
          setTimeout(() => {
            //加了个定时器，避免了表单初始化的时候报错
            this.form.setFieldsValue({
              workflowName: this.fields.workflowName.value,
              workflowRemark: this.fields.workflowRemark.value,
              workSwitch: this.fields.workSwitch.value
            });
          }, 0);
        });
        //  this.fields.workflowName.value = record.wftName
        //  this.fields.workflowRemark.value = record.remark

        this.form = this.$form.createForm(this, {
          onFieldsChange: (_, changedFields) => {
            this.$emit("change", changedFields);
          },
          mapPropsToFields: () => {
            return {
              workflowName: this.$form.createFormField({
                value: this.fields.workflowName.value
              }),
              workflowRemark: this.$form.createFormField({
                value: this.fields.workflowRemark.value
              }),
              workSwitch: this.$form.createFormField({
                value: this.fields.workSwitch.value
              })
            };
          },
          onValuesChange: (_, values) => {
            console.log(values);
            this.workFlowRevise.wftId = record.wftId;
          }
        });
      }
    },
    // 修改的弹出框确定按钮
    handleOkReviseworkflow(e) {
      console.log("修改的弹出框确定按钮");
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          this.workFlowRevise.remark = values.workflowRemark;
          this.workFlowRevise.wftName = values.workflowName;

          this.workFlowRevise.interrupt = values.workSwitch == true ? 1 : 0;
          // this.$store.commit('update', values);
          this.visibleReviseworkflow = false;
          console.log(this.workFlowRevise);
          WorkFlowListupdate(this.workFlowRevise).then(res => {
            console.log(res);
            this.$message.success("修改工作流成功");
            this.getListdata();
          });
        }
      });
    },
    ReviseRole(record) {
      console.log(record.roleId);

      this.visibleRolename = true;
      console.log(record);
      if (record.roleId) {
        //  this.visibleReviseworkflow = true
        this.Rolerevise.roleId = record.roleId;
        this.fieldsRole.roleName.value = record.roleName;
        this.fieldsRole.roleMark.value = record.roleMark;

        setTimeout(() => {
          //加了个定时器，避免了表单初始化的时候报错
          this.formRole.setFieldsValue({
            roleName: this.fieldsRole.roleName.value,
            roleMark: this.fieldsRole.roleMark.value
          });
        }, 0);

        this.formRole = this.$form.createForm(this, {
          onFieldsChange: (_, changedFields) => {
            this.$emit("change", changedFields);
          },
          mapPropsToFields: () => {
            return {
              roleName: this.$form.createFormField({
                value: this.fieldsRole.roleName.value
              }),
              roleMark: this.$form.createFormField({
                value: this.fieldsRole.roleMark.value
              })
            };
          },
          onValuesChange: (_, values) => {
            console.log(values);
          }
        });
      }
    },
    //角色修改的确认按钮
    handleOkRole(e) {
      this.handleSubmitRole(e);
    },
    handleSubmitRole(e) {
      e.preventDefault();
      this.formRole.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
          this.Rolerevise.roleName = values.roleName;
          this.Rolerevise.roleMark = values.roleMark;
          console.log(this.Rolerevise);
          this.visibleRolename = false;
          RoleListupdate(this.Rolerevise).then(res => {
            if (res.data.message == "success") {
              this.$message.success("修改角色成功");
              this.getRoleListData();
            }
          });
        }
      });
    },
    userNameError() {
      // const { getFieldError, isFieldTouched } = this.formadd;
      // return isFieldTouched('workflowName') && getFieldError('workflowName');
    },
    addchangeInput() {
      // console.log('失去焦点报红');
      this.formadd.validateFields(["workflowName"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
    },
    addchangeTextarea() {
      this.formadd.validateFields(["workflowRemark"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
    },
    addNewMeunlogochangeInput() {
      this.formaddmenu.validateFields(["workflowName"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
    },
    addNewMeunNamechange() {
      this.formaddmenu.validateFields(["workflowRemark"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
    },
    onEdit(targetKey, action) {
      this[action](targetKey);
    },
    dynamicAdd(wftId) {
      this.$store.dispatch("workflowListdynamicAdd", wftId);
    }
  }
};
</script>


